* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
}

.favoriteBooks_wrapper {
  display: flex;
  flex-wrap: wrap;
}

.favoriteBooks_empty {
  font-family: Roboto;
  font-size: 18px;
  color: #000000;
  margin-top: 10px;
  margin-left: 30px;
}

.favoriteBooks_empty_link {
  cursor: pointer;
  color: #996b60;
  font-weight: bold;
}
